          </p>
          <div v-if="item.al.id !== songId" class="newsong-play"></div>
          <div
            v-else
            class="newsong-play active"
            :class="{ playing: playing }"
          >
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // artist?id=31165848
  props: {
    songId: Number,
    playing: Boolean,
    res: null,
    // autoplay: Boolean,
  },
  data: function () {
    return {
      artist: null,
      isSee: true,
    };
  },
  beforeRouteEnter(to, from, next) {
    // console.log(to.query.id);
    next((vm) => {
      vm.axios
        .get("https://apis.netstart.cn/music/artists", {
          params: {
            id: to.query.id,
          },
        })
        .then((res) => {
          // console.log(res);
          // console.log(res.data.hotSongs[0].sq);
          //   for (let i = 0; i <5; i++) {
          //     vm.artist.push(res.data.hotSongs[i])
          //   }
          vm.artist = res.data;
        });
    });
  },
};
</script>

<style lang="less" scoped>
.box {
