      overflow: hidden;
      // white-space: wrap;
      // text-overflow: ellipsis;
      p {
        font-size: 14rem;
        font-weight: 500;
        color: #666;
      }
    }
    .active {
      height: 100%;
    }
    .dianji {
      font-size: 14rem;
      // text-align: center;
      margin-top: 5rem;
      height: 30rem;
      display: flex;
      justify-content: center;
      align-content: center;
      align-items: center;
      svg {
        width: 30rem;
        height: 30rem;
      }
    }
    .shouhui {
      font-size: 14rem;
      text-align: center;
      margin-top: 5rem;
      height: 30rem;
      display: flex;
      justify-content: center;
      align-content: center;
      align-items: center;
      svg {
        width: 30rem;
        height: 30rem;
      }
    }
  }
  h1 {
    font-size: 18rem;
    margin: 15rem;
    font-weight: bold;
  }
  .artistbg {
    width: 100%;
    // height: 310rem;
    // position: relative;
