      width: 100%;
      position: relative;
      p {
        font-size: 16rem;
      }
    }
    // margin: 5rem 0;
    border-bottom: 1rem solid rgba(128, 128, 128, 0.082);
    div > p:nth-of-type(2) {
      font-size: 12rem;
      color: gray;
      line-height: 2rem;
      margin-bottom: 1rem;
    }
  }
  // .bg2 {
  // margin: 0;
  // display: inline-block;
  // width: 24rem;
  // height: 8rem;
  // color: white;
  // background-size: 740%;
  // background-repeat: no-repeat;
  // background-position: 2rem 0rem;
  // background-image: url("../assets/1.png");
  // }
  .hot {
    background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
    //   width: 17px;
    //   height: 12px;
    width: 16rem;
    height: 16rem;
    display: inline-block;
    background-size: 200rem;
    margin-right: 5rem;
    background-position-y: 6.5rem;
  }
  @keyframes playingrunning {
    from {
      transform: rotateX(15deg);
    }
    to {
      transform: rotateX(60deg);
    }
  }
  .newsong-play {
    background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
    background-position: -24px 0;
    background-size: 166px 97px;
    width: 22rem;
    height: 22rem;
    position: absolute;
    right: 10rem;
    top: 50%;
    transform: translateY(-50%);
    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      span {
        width: 11%;
        height: 100%;
        background: red;
        animation: playingrunning 0.8s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }

      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
